import { BellAlertIcon } from '@heroicons/react/24/outline';

export default function Hero() {
  return (
    <div className="bg-white py-16 sm:py-24">
      <div className="mx-auto max-w-7xl px-6 lg:px-8">
        <div className="mx-auto max-w-3xl text-center">
          <h1 className="text-4xl font-bold tracking-tight text-gray-900 sm:text-5xl mb-8">
            عَرَبَاي <span className="text-indigo-600">ArabAi</span>
          </h1>
          <p className="mt-6 text-lg leading-8 text-gray-600">
            أول منصة عمل عربية تجمع طالبي خدمات الذكاء الاصطناعي بالمصممين والمبرمجين 
            من مختلف التخصصات والمستويات والأسعار
          </p>
          
          <div className="mt-10 px-4 py-3 bg-amber-50 border border-amber-200 rounded-lg">
            <div className="flex items-center justify-center gap-2 text-sm text-amber-800">
              <BellAlertIcon className="h-5 w-5 flex-shrink-0" />
              <p>
                المنصة غير مسؤولة عن نوعية وسوء نية الأعمال التي يتم طلبها أو إنجازها.
                نحاول منع أي تجاوزات ويمكنكم مساعدتنا بالضغط على جرس الإبلاغ إذا رأيتم ما يريب.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 